.table {
  width: 70%;
  margin: 20px auto;
  border-collapse: collapse;
}

.head th {
  font-weight: bold;
  font-size: 30px;
  text-align: center;
  padding: 12px;
}

.row {
  border-bottom: 1px solid #f2f2f2;
}

.row td {
  padding: 8px;
  font-size: 20px;
  text-align: center;
}
.row:last-child {
  border-bottom: 0px solid red;
}

.logo {
  width: 30%;
  display: flex;
  align-items: center;
  gap: 40px;
  text-align: left;
  margin: 0 auto;
}

.symbol {
  width: 30px;
  display: flex;
  margin: 0 auto;
  justify-content: center;
}

